body {
    margin: 0;
    font-family: 'Inter', system-ui;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #ffffff;
  }
  
  * {
    font-family:'Inter', system-ui;
  }

  .content {
    .services {
      margin: 0px;
      background: #F4F8FB;
      box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
      border-radius: 20px;
      margin-left: 80px;
      width: 1440px;
      height: 740px;
    }

    .servicesContent {
        background: #ffffff;
        box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
        border-radius: 10px;
        margin: 20px;
        width: 1400px;
        height: 700px;

      .servicesHeader {
        border-bottom: 1px solid #eaeaea;
        width: 1400px;
        height: 70px;
        padding: 20px;

        h3 {
          margin: 0px;
        }

        span {
          border-radius: 5px;
          background: #66CDAA;
          padding-left: 10px;
          padding-right: 10px;
          padding-top: 5px;
          padding-bottom: 5px;
          font-size: 14px;
          color:#ffffff;
          cursor: pointer;
        }

        span:hover {
          background-color: #72e6bf;
        }
      }
      
      .card {
        margin: 20px;
        padding: 20px;
        background: #ffffff;
        border: 1px solid #f3f3f3;
        border-radius: 20px;
        width: 240px;
        height: 275px;
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        transition: box-shadow 0.2s ease-in-out, transform 0.2s ease-in-out;
        cursor: pointer;
        
        &:hover {
          box-shadow: 0px 20px 35px rgba(0, 0, 0, 0.06);
          transform: translateY(-5px);
        }

        img {
          margin: 0px;
          border-radius: 20px;
        }

        h5 {
          margin: 5px;
          margin-bottom: 0px;
          text-align: center;
          font-weight: 400;
          font-size: 16px;
        }

        p {
          margin: 0px;
          text-align: center;
          opacity: 0.5;
          font-size: 14px;
        }
      }  
    }
  }